  width: 100%;
  height: 100%;
  z-index: 9990;
  background-color: rgba(0, 0, 0, 0);
}
.success {
  height: 150px;
  width: 370px;
  border: 1px solid #DCDCDC;
  border-radius: 5px;
  position: fixed;
  margin: auto;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 9991;
  background-color: white;
}
.success .loadinganimate {
    position: relative;
    width: 40px;
    margin: auto;
    padding: 20px 0;
}
.success .loadinganimate .loadingimg {
      width: 40px;
      height: 40px;
}
.success .signonsuccess {
    font-size: 16px;
    text-align: center;
}
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p,
blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, font, img,
ins, kbd, q, s, samp, small, strike,
strong, sub, sup, tt, var, dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
center, u, b, i {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-weight: normal;
  font-style: normal;
  font-family: inherit;
  vertical-align: baseline; }

body {
  line-height: 1;
  background: #f6f6f6;
  color: #1a1a1a; }

:focus {
  outline: 0; }

ol, ul {
  list-style: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

blockquote:before, blockquote:after, q:before, q:after {
  content: ""; }

blockquote, q {
  quotes: ""; }

input, textarea {
  margin: 0;
  padding: 0; }

hr {
  margin: 0;
  padding: 0;
  border: 0;
  color: #000;
  background-color: #000;
  height: 1px; }

::-webkit-input-placeholder {
  color: #9e9c9c; }

:-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #9e9c9c; }

::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #9e9c9c; }

:-ms-input-placeholder {
  /* Internet Explorer 10+ */
  color: #9e9c9c; }

.main {
  padding-top: 50px;
  width: 1000px;
  margin: auto; }

.text-center {
  text-align: center; }
